import React from "react";
import { Table } from 'react-bootstrap';
import { connect, useDispatch, useSelector } from "react-redux";

function Cart(props) {

  let state = useSelector((state) => state)
  console.log(state);
  let dispatch = useDispatch();

  return (
    <div>
      <Table responsive="sm">
        <thead>
          <tr>
            <th>#</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {
            state.reducer.map((a, i) => {
              return (
                <tr key={i}>
                  <td>{ a.id }</td>
                  <td>{ a.name }</td>
                  <td>{ a.quan }</td>
                  <td><button onClick={() => { dispatch({ type: '', payload : (a.id) }) }}></button></td>
                  <td><button onClick={()=>{ dispatch({ type : '', payload : (a.id) }) }}></button></td>
                </tr>
              )
            })
          }
        </tbody>
      </Table>
      {state.reducer2 === true
        ? <div className="my-alert2">
          <p>   20%</p>
          <button onClick={()=>{dispatch({type:''})}}></button>
        </div>
        : null
      }
    </div>
  )
}

